import React from 'react';
import { Card } from 'react-bootstrap';
import './home.scss';

export function Home() {
    return (
        <div className="container">
            <div className="row">
                <div className="col-lg-12">
                    <Card>
                        <Card.Body>
                            <h3 className="home-h3">Welcome to my blog</h3>
                            <p>The purpose of this blog is to showcase my free time projects and provide my resumé for those interested. There might occasionally be posts about my thoughts and/or experiences in my day to day life as a developer. </p>
                        </Card.Body>
                    </Card>
                </div>
            </div>           
        </div>
    )
}