import React, { useState, useEffect } from 'react';
import { Navbar, NavDropdown, Nav } from 'react-bootstrap';
import { useLocation } from 'react-router-dom';
import './core.scss';
export function Core() {
    const [activeKey, setActiveKey] = useState("");
    let location = useLocation();

    useEffect(() => {
        console.log(location.pathname)
        setActiveKey(location.pathname);
    }, [])

    return (

        <div>
            <Navbar variant="dark" expand="lg">
                <Navbar.Brand>Andreas Björkegren</Navbar.Brand>
                <Navbar.Toggle aria-controls="basic-navbar-nav" />
                <Navbar.Collapse id="basic-navbar-nav">
                    <Nav className="mr-auto" activeKey={activeKey} onSelect={(selectedKey) => setActiveKey(selectedKey)}>
                        <Nav.Link href="#/home">Home</Nav.Link>
                        <Nav.Link href="#/about">About</Nav.Link>
                        <Nav.Link href="#/resume">Resumé</Nav.Link>
                        <NavDropdown title="Portfolio" id="basic-nav-dropdown">
                            <NavDropdown.Item href="#/portfolio/zen-demo">ZenPad online demo</NavDropdown.Item>
                            <NavDropdown.Item href="https://simmer.io/@greenbjork/aerohero" target="blank">Aero Hero</NavDropdown.Item>
                            <NavDropdown.Item href="https://soundcloud.com/user-396048806" target="blank">SoundCloud</NavDropdown.Item>
                            <NavDropdown.Divider />
                            <NavDropdown.Item disabled="disabled">More to come..</NavDropdown.Item>
                        </NavDropdown>
                    </Nav>
                    <Nav defaultActiveKey="mailto:andreas.bjorkegren.public@gmail.com">
                        <Nav.Link href="mailto:andreas.bjorkegren.public@gmail.com" >
                            Contact me
                        </Nav.Link>
                    </Nav>
                </Navbar.Collapse>
            </Navbar>

        </div>
    )
}