import React from 'react';
import { ZenPad } from './components/zen-pad/zenPad';
import './App.scss';
import {
  HashRouter as Router,
  Switch,
  Route,
  Redirect,
} from "react-router-dom";
import { Core } from './components/core/core'
import { Home } from './components/home/home';
import { Resume } from './components/resume/resume';
import { About } from './components/about/about';


function App() {
    return (
      <div>
          <Router>
            <Core>
            </Core>
            <div className="container">
              <div className="col">
                <Switch>
                  <Route path="/portfolio/zen-demo">
                    <ZenPad></ZenPad>
                  </Route>
                  <Route path="/home">
                    <Home></Home>
                  </Route>
                  <Route path="/about">
                    <About></About>
                  </Route>
                  <Redirect exact from="/" to="home" />
                  <Route path="/resume">
                    <Resume></Resume>
                  </Route>
                </Switch>
              </div>
            </div>
          </Router>
      </div>
    );
  }

export default App;
