import React, { useState, useEffect } from 'react'
import './zenPad.scss'
import { useDebounce } from './useDebounce'
import { Card } from 'react-bootstrap';


export function ZenPad() {
    const [zenInput, setZenInput] = useState('');
    const [isResetting, setIsResetting] = useState(false);
    const [className, setClassName] = useState('padInput');
    const debounceInput = useDebounce(zenInput, 5000);

    useEffect(
        () => {
            if (debounceInput) {
                if (!isResetting) {
                    console.log("debounced and resetting", debounceInput)
                    letGo()
                }

            }

        }, [debounceInput]
    )

    function letGo() {
        setIsResetting(true);
        setClassName('padInput hidden');
        setTimeout(reset, 5000)
        setIsResetting(false);
    }

    function reset() {
        setZenInput('');
        setClassName('padInput');
    }



    return (
        <Card>
            <Card.Body>
                <div>
                    <h1 className="zen-h">ZenPad</h1>

                    <p className="zen-p">Write down your thoughts that you need to get out of your head and watch them fade away.</p>
                    <div className="padWrapper">
                        <textarea value={zenInput} type="text" rows="15" autoFocus className={className}
                            onChange={e => setZenInput(e.target.value)}>
                        </textarea>
                    </div>
                </div>
            </Card.Body>
        </Card>

    )
}