import React from 'react';
import { Card } from 'react-bootstrap';
import './about.scss';

export function About() {
    return (
        <div className="container">
            <div className="row">
                <div className="col-lg-12">
                    <Card>
                        <Card.Body>
                            <h3 className="about-h3">This is Andreas Björkgren</h3>
                            <h5>My philosophy</h5>
                            <p>To summarize this 26-ish year old in one word: <b>Failure</b>.</p>
                            <p> During my whole life I've failed countless of times and it's been amazing. If someone is better than you, it's not that you are worse than them or that you are less valuable.
                            It's all about the amount of time invested and the amount of failures you learn from. And since the only person you should compare yourself to, is yourself, it makes even more sense.
                            For example, today you are amazing at walking. When you were a newborn, you couldn't even hold your own head up. Since you were a newborn you've put in countless of hours of falling to the ground and trying to stand up and walk.
                            Look at you today, you don't even have to think about what leg to put in front of the other, you're amazing at doing it! I think that mindset is crucial for self-improvement,
                                we should all strive to be amazing at stuff and never forget that failing brings us one step closer to amazing.</p>
                            <h5>My everyday life today</h5>
                            <p>I live a very average day to day life. I have my work during the day and then time for my family (girlfriend + our <a data-toggle="tooltip" title="he's the cutest rabbit in the world">rabbit</a>) and hobbies during the evenings.
                                I'm currently learning game development in Unity which is awesome. There's som much material and help to get online. So you can be a complete novice and actually create a game from scratch!! 
                                I also really enjoy learning new ways to develop software. Everything from the latest .Net Core to React library. I don't have a deep understanding of everything new, 
                                but I love to at least have a basic understanding of the latest mainstream software development changes or as we say in Swedish "Hänga med i svängarna".</p>

                            <h5>More about me</h5>
                            <p>I'm a jolly good fella born, raised and based in Stockholm, Sweden. Ever since I was a kid I've had two major interests,
                                Music and computers/computer games.</p>
                            <h6>Computers</h6>
                            <p>
                                 After playing Warcraft 3 together with my friends after school, I was hooked on gaming. My interest for computers came after I put together my first computer all by myself.
                                I didn't have much knowledge about computers back then and I was terrified when it actually came time to build it. After I've put together everything and pressed the power button "DRUMROLL"... the computer didn't post.
                                It felt like my whole world crumbled. I knew it, I wasn't good enough, I couldn't possibly have the skills to build my own computer.
                                After some more self loathing I finally decided to troubleshoot the damn thing. And a few YouTube videos later and some DRAM swapping, my first self built computer was up and running.
                                It somehow became a metaphoric milestone in my life. This task that seemed impossible for 15-year old me, was suddenly possible. From that moment on I knew that if I just persevere and allow myself to fail I can accomplish anything I set out to do.
                            </p>
                            <h6>Music</h6>
                            <p>I picked up the piano fairly quick and learned to play my favorite movie and game scores and after a while I realized that it's quite the challenge to bring a piano with you to share your music.
                            Thus was my guitar hobby picked up. After countless of hours in front of YouTube I could learn my favorite songs and share them with the people around me (wether they liked it or not).
                            During my later years I've also picked up music production in Ableton, and I <b>LOVE IT</b>.
                            I think it's amazing that you can create this huge music pieces straight from your mind and some practical skills in a DAW.
                                  </p>
                        </Card.Body>
                    </Card>
                </div>
            </div>
        </div>
    )
}