import React from 'react';
import { Card, Media } from 'react-bootstrap';
import './skills.scss';
import angular from '../../../assets/icons/angular.png'
import cSharp from '../../../assets/icons/cSharp.svg'
import dotNet from '../../../assets/icons/dot-net.png'
import msSql from '../../../assets/icons/sql-server.png'
import react from '../../../assets/icons/react.png'
import google from '../../../assets/icons/google.png'



export function Skills() {
    return (
        <div>
            <h3 className="resume-h3">Skills</h3>
            <ul className="list-unstyled">
                <Media as="li">
                    <img
                        width={64}
                        height={64}
                        className="mr-3"
                        src={angular}
                        alt="Angular logo"
                    />
                    <Media.Body>
                        <h5>Angular 8+</h5>
                        <p>I have worked with angular to produce web applications for administrating Espresso House pos inventory management. I was also a part of creating the back office for Espresso House global loyalty program.</p>
                    </Media.Body>
                </Media>

                <Media as="li">
                    <img
                        width={64}
                        height={64}
                        className="mr-3"
                        src={dotNet}
                        alt=".Net core logo"
                    />
                    <Media.Body>
                        <h5>.NET Core</h5>
                        <p>We used .NET Core 2.0 to build the backend for Espresso House's global loyalty program. It includes everything from client side API:s to database management. Our next task is to upgrade existing solutions to most recent .NET Core. </p>
                    </Media.Body>
                </Media>

                <Media as="li">
                    <img
                        width={64}
                        height={64}
                        className="mr-3"
                        src={cSharp}
                        alt="C# logo"
                    />
                    <Media.Body>
                        <h5>C#</h5>
                        <p>Using C# I have created console applications that run scheduled batch jobs supporting Espresso House global loyalty program.</p>
                    </Media.Body>
                </Media>

                <Media as="li">
                    <img
                        width={64}
                        height={64}
                        className="mr-3"
                        src={msSql}
                        alt="MS SQL Server logo"
                    />
                    <Media.Body>
                        <h5>MS SQL Server</h5>
                        <p>I have been using MS SQL Server for creating databases and tables that I then have used in API:s and console applications. I have also worked with building and deploying integration packages.</p>
                    </Media.Body>
                </Media>

                <Media as="li">
                    <img
                        width={64}
                        height={64}
                        className="mr-3"
                        src={react}
                        alt="React logo"
                    />
                    <Media.Body>
                        <h5>React 16.0+</h5>
                        <p>During my spare time I have looked into React and created a POC for a survey solution intended for a driving school. Also this site was created with React.</p>
                    </Media.Body>
                </Media>

                <Media as="li">
                    <img
                        width={64}
                        height={64}
                        className="mr-3"
                        src={google}
                        alt="google cert logo"
                    />
                    <Media.Body>
                        <h5>Mobile web specialist</h5>
                        <p>In february 2020 I received my google certification. You can read about the certification <a href="https://developers.google.com/certification/mobile-web-specialist" target="blank">here!</a></p>
                    </Media.Body>
                </Media>
            </ul>
        </div>
    )
}